body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}#root{background-color:#414141}

#root{display:flex;justify-content:center}

.Layout{height:100vh;display:flex;flex-direction:column;border:1px solid white;width:60%;padding:15px}.Layout.Main{display:flex;flex-direction:column;flex-grow:1}


.Header.top{display:flex;justify-content:space-between;padding:10px;align-items:center}

.Categories{display:flex;flex-direction:row;justify-content:space-around;align-items:center;border:1px solid;height:40px;background-color:turquoise;font-weight:400}.Categories .item{line-height:40px;width:100%}.Categories .active{background-color:grey;color:white}

.Logo{display:flex}.Logo.blue{color:turquoise}


.ActiveQuiz{display:flex;flex-direction:row;margin:20px 0;border:1px solid white;border-radius:3px}.QuizCard{display:flex;flex-direction:column;justify-content:space-between;width:100%}.QuizAnswer{margin:auto;font-size:16px;font-weight:bold}



.Answers.top{display:flex;justify-content:space-around}.row{margin-right:0}


.list-group-item{background-color:#878a90;cursor:pointer}.list-group-item:hover{background-color:#d2d6da}.li-btn{display:inline-block;width:10px;height:10px;margin-right:15px;border-radius:50%;transition:.1s;background-color:white}.li-btn.error{background-color:red}.li-btn.success{background-color:turquoise}

.Description{background-color:#878a90;border-radius:3px}.BirdCard{display:flex;flex-direction:column;margin:10px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.BirdCard .Title{display:flex}.BirdCard .Title .ShortDescription{margin:10px}

.Button{display:flex;justify-content:center;width:100%;margin:15px 0}.Button .btn{width:100%;border-color:#878a90}.Button .btn:hover{background-color:#d2d6da}

.FinishedQuiz{padding:20px;color:white;border:2px solid white;border-radius:5px;box-sizing:border-box;margin:0 10px}.FinishedQuiz p{text-align:center}.FinishedQuiz .kingOfTheGame{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:10px 0}

